* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

header {
  background-color: rgb(255, 255, 255);
}

li {
  list-style: none;
}

a {
  color: rgb(255, 255, 255);
  text-decoration: none;
}
.container{
  width: 100wh;
  height: 60px;
}

.navbar {
  min-height: 5vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 24px;

  z-index: 3;
  width: 100%;
  background-color: rgb(22, 33, 62);

  position: fixed;
}

.nav-branding {
  height: 3vw;
  display: flex;
  -webkit-filter: brightness(0) invert(1);
          filter: brightness(0) invert(1);
  margin-left: 16px;
  margin-bottom: 5px;
}

.nav-item {
  transition: 0.1s ease;
}

.nav-item:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.navbar img:hover {
  cursor: pointer;
}

@media (max-width: 900px) {
  .nav-item {
    margin: 16px 0;
  }
}

@media (max-width: 600px) {
  .nav-branding {
    height: 40px;
  }
}

@media (max-width: 425px) {
  .nav-branding {
    height: 40px;
  }
}

.hamburger {
  display: none;
  cursor: pointer;
}

.bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  transition: all 0.3s ease-in-out;
  background-color: rgb(255, 255, 255);
}

@media (max-width: 940px) {
  .hamburger {
    display: block;
  }

  .hamburger.active .bar:nth-child(2) {
    opacity: 0;
  }
  .hamburger.active .bar:nth-child(1) {
    -webkit-transform: translateY(8px) rotate(45deg);
            transform: translateY(8px) rotate(45deg);
  }
  .hamburger.active .bar:nth-child(3) {
    -webkit-transform: translateY(-8px) rotate(-45deg);
            transform: translateY(-8px) rotate(-45deg);
  }
}

.nav-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 30px;
  gap: 30px;
  margin-right: 36px;
  font-size: 16px;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  font-style: normal;
}

@media (max-width: 940px) {
  .nav-menu {
    font-size: 16px;
    position: fixed;
    left: -100%;
    top: 6vh;
    flex-direction: column;
    background-color: rgb(22, 33, 62);
    width: 100%;
    text-align: center;
    transition: 0.3s;
  }

  .nav-menu.active {
    position: fixed;
    left: 0;
  }
}


