.upperWave-container{
    width: 100%;
    overflow: hidden;
    display: flex;
}

.upper-block-container{
    height: 5vh;
    width: 100%;
    background-color: #16213E;

    position: absolute;
    top: 100vh;
}

.waveUpper{
    width: 100%;
    height: 100px;
    background-size: 1000px 100px;
    background-color: #16213E;

    position: absolute;
    top: 104vh;
}

.wave.wave1Upper{
    animation: animate 50s linear infinite;
}

@keyframes animate {
    0%{
        background-position-x: 0;
    }
    
    100%{
        background-position-x: 1000px;
    }
}


@media only screen and (max-width: 550px) {
    .footer-container{
        height: 100vh;
    }

    .wave{
        display: none;
    }


}